const luajson = require("lua-json");
const luaparse = require("luaparse");

class LuaData {
    constructor(data) {
        this.data = data;
    }

    /**
     * lua table => json
     */
    toJSON() {
        return luajson.parse(this.data);
    }

    /**
     * js object => lua table
     */
    toLua() {
        return luajson.format(this.data);
    }

    /**
     * lua code ast builder
     * more info => https://www.npmjs.com/package/luaparse
     */
    toAST(opt) {
        return luaparse.parse(this.data, opt);
    }

    /**
     * //TODO:data to csv
     */
    toCSV(opt) {
        // wait for some day
        return this.data;
    }
}

class LuaReader {
    /**
     * init return a promise,not the instance
     */
    constructor() {
        return this.init();
    }

    /**
     * check browser support
     * @returns {boolean}
     */
    static check() {
        return FileReader && TextDecoder ? true : false;
    }

    /**
     * no errors!!
     * @returns {promise}
     */
    init() {
        return new Promise((resolve, reject) => {
            if (LuaReader.check()) {
                resolve(this);
            } else {
                reject(new Error("Browser not support"));
            }
        });
    }

    /**
     * parse file
     * @param {string} selector document element id
     * @param {function} callback do to every file
     * @returns {object} this instance
     */
    watch(selector, callback, encoding = "gbk") {
        const decoder = new TextDecoder(encoding);

        // get the element
        let ele = document.getElementById(selector);

        // map the file
        ele.addEventListener("change", function () {
            // get file list
            let datalist = Array.from(ele.files);
            datalist.forEach((file) => {
                // read content
                let reader = new FileReader();
                reader.readAsArrayBuffer(file);
                reader.onload = function () {
                    // decode content
                    let data = decoder.decode(reader.result);
                    let _data = new LuaData(data);

                    // do to data
                    callback && callback(_data);
                };
            });
        });
    }
}

module.exports = { LuaReader, LuaData };
